import React from 'react'
import { NavLink } from 'react-router-dom';
import { HashLink as Link } from 'react-router-hash-link';
import { Box,Text,Button } from '@chakra-ui/react';
import Modalsec from "./Modal"
import Resume from "../Components/Akshay-Kumar-Resume.pdf"
import { DownloadIcon } from '@chakra-ui/icons';
const Navbar = () => {
  return (
    <Box 
     bgColor="black"
      w={{ base: "100%", md :"100%", lg: '100%' }}>
        <Box 
         w={{ base: "90%", md :"90%", lg: '90%' }} 
         alignItems="center" display="flex" 
          justifyContent="space-between"  margin="auto">
          <Box w={{ base: "45%", md :"30%", lg: '30%' }}  textAlign="left"  >
          <Text  fontSize='2xl' color="#9DECF9" fontWeight="bold"  >PORTFOLIO</Text>
          </Box>
          <Box w={{ base: "70%", md :"80%", lg: '70%' }}  display={{ base: "none", md :"flex", lg: "flex"}}
           justifyContent="space-between" alignItems="center" >

         <Link  style={{color:"white"}}  to="#Home" >Home</Link>
         <Link  style={{color:"white"}}  to="#About" >About</Link>
        <Link style={{color:"white"}}  to="#Contact" >Contact</Link>
         <Link style={{color:"white"}} to="#project" >Project</Link>
         <Link style={{color:"white"}} to="#skills"  >Skills</Link>
         {/* <Button bgColor="#CBD5E0"  colorScheme='whatsapp' border="1px solid green" variant='outline'>
          <NavLink style={{color:"white"}} to="Akshay-Kumar-Resume.pdf"  >Resume</NavLink></Button> */}
                   <a href= { Resume}download ><Button bgColor="#CBD5E0"  colorScheme='whatsapp' border="1px solid green" variant='outline'>
    Resume      </Button></a>        
         </Box>
         <Box display={{ base: "flex", md :"none", lg: "none"}}  >
        <Modalsec />
         </Box>
        
        </Box>
    </Box>
  )
}


export default Navbar

