
import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';

import {
 
  Button,
  Box,
  Flex,
  Stack,
  Heading,
  Text,
  Image,
  Container

} from '@chakra-ui/react'

 const Contact = () => {
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_kuwlbr3',
     'template_k8k3ad1',
      form.current,
       '9vXfjwYOtckFeEpW1'
       )
      .then((result) => {
          console.log(result.text);
          alert("Message Sent")
      }, (error) => {
          console.log(error.text);
      });
  };
  return (
    <section id="Contact" >
    <Box   display="flex" flexDirection="row" margin="auto" >
       <form ref={form} onSubmit={sendEmail}>
        <Stack spacing={6} >
        <Box>
      <label>Name</label>
      <input type="text" name="user_name" />
      </Box>
      <Box>
      <label>Email</label>
      <input type="email" name="user_email" />
      </Box>
      <Box>
      <label>Message</label>
      <textarea name="message" />
      </Box>
      <Box>
      <input type="submit" value="Send" />
      </Box>
      </Stack>
    </form>
    </Box>
    </section>
  )
}

export default Contact